import React, { useState } from 'react';
import { useEffect } from 'react';
import { Col, Form, Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import { auth, db } from '../../../firebase';
import { setAuthenticated } from '../../../redux/user/action';
import ReusableButton from '../../../reusable/button';
import SignFormGroup from '../../../reusable/sign_form/signFormGroup';


const SignUpFormBody = ({ setAuthenticated }) => {
    const [formData, setFormData] = useState({});
    const [user, setUser] = useState('');

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        })
    }

    const handleSignup = (e) => {
        e.preventDefault();

        auth.createUserWithEmailAndPassword(formData.email, formData.password)
            .then(user => {
                setUser(user);
                setAuthenticated(user)
            })
            .catch((err => console.log(err)))
    }

    useEffect(() => {
        auth.onAuthStateChanged(authUser => {
            if (authUser) {
                setUser(authUser)
                authUser.updateProfile({
                    displayName: formData.name
                })

            } else {
                setUser(null);
            }
        })
    }, [user]);

    const formFields = [
        {
            classes: {
                formControl: '',
                label: '',
                prepend: 'fas fa-user',
            },
            controlId: 'email',
            type: 'email',
            name: 'email',
            label: 'Email',
            labelFor: 'email',
            placeholder: 'Enter Your Email',
        },
        {
            classes: {
                formControl: '',
                label: '',
                prepend: 'fas fa-user',
            },
            controlId: 'sign-up-name',
            type: 'text',
            name: 'name',
            label: 'Name',
            labelFor: 'name',
            placeholder: 'Enter Your Name',
        },
        {
            classes: {
                formControl: '',
                label: '',
                prepend: 'fas fa-key',
            },
            controlId: 'password',
            type: 'password',
            name: 'password',
            label: 'Password',
            labelFor: 'password',
            placeholder: 'Password',
        },
        {
            classes: {
                formControl: '',
                label: '',
                prepend: 'fas fa-key',
            },
            controlId: 'confirm',
            type: 'password',
            name: 'confirm',
            label: 'Confirm Password',
            labelFor: 'confirm',
            placeholder: 'Re-Password',
        },
        {
            classes: {
                formControl: 'form-checkbox agree-checkbox d-none',
                label: 'form-checkbox-label d-block font-size-md mt-4 mb-0',
                prepend: 'checkbox-prepend',
            },
            controlId: 'form-checkbox',
            type: 'checkbox',
            name: 'agree_checkbox',
            label: 'I agree to the terms of application',
            labelFor: 'form-checkbox',
        },
    ]
    console.log(user)
    return (
        <Form className="sign-up-form d-flex flex-md-row flex-column align-items-center flex-wrap mt-4">
            <Row>
                {
                    formFields.map((f, index) => {
                        return (
                            <Col key={index} md={6} lg={5} xl={6} className="mt-4">
                                <SignFormGroup
                                    classes={f.classes}
                                    controlId={f.controlId}
                                    type={f.type}
                                    name={f.name}
                                    label={f.label}
                                    labelFor={f.labelFor}
                                    placeholder={f.placeholder}
                                    onChange={handleChange}
                                />
                            </Col>
                        );
                    })
                }
                <a href="#" className="form-link">Microsoft Terms</a>
            </Row>
            <ReusableButton
                classes={{
                    btn: 'form-btn bg-success mt-5 border-0 font-weigh-md'
                }}
                to="#"
                type="submit"
                label="sign up"
                text="Sign Up Now !"
                onClick={handleSignup}
            />
        </Form>
    );
}

export default connect(null, { setAuthenticated })(SignUpFormBody);
